<template>
  <div class="hello">

  <div class="tasktable" align="center" v-if="tasks.length > 0" >
    <el-table ref="singleTable" :data="tasks" :show-header="show_header" highlight-current-row @current-change="handleCurrentChange" style="width: 20%">
    <el-table-column
      type="selection"
      width="55">
    </el-table-column>

    <el-table-column width="200" :context="_self" inline-template label="Title" >
    <div v-if="row.Done" > <del>{{row.Title}}</del></div>
    <div v-else> {{row.Title}} </div>
    </el-table-column>
    </el-table>
    </div>

    <el-input type="text" v-model="newTask.Title" @keyup.enter.native="createTask" placeholder="请输入内容">
      <el-button slot="append" icon="plus" type="primary" @click="createTask">New Task</el-button>
    </el-input>
  </div>
</template>

<script>

export default {
  data () {
    return {
      newTask: {},
      tasks: [],
      show_header: false
    }
  },

  created () {
    this.newTask = {}
    this.refresh()
  },

  methods: {

    refresh () {
      this.$http.get('/task').then(function (res) {
        // console.log(res.data.Tasks)
        this.tasks = res.data.Tasks ? res.data.Tasks : []
      })
    },

    createTask () {
      if (!this.newTask.Title.trim()) {
        this.newTask = {}
        return
      }
      this.newTask.checked = false

      this.$http.post('/task', {Title: this.newTask.Title}).then(function (res) {
        this.refresh()
        this.newTask = {'Title': ''}
      }, function (err) {
        console.log(err)
      })
    },

    changeTaskState (task) {
      var data = {ID: task.ID, Title: task.Title, Done: !task.Done}
      console.log('to change info')
      console.log(data)
      this.$http.put('/task/' + task.ID, data).then(function (res) {
        this.refresh()
        // console.log(task.ID)
      }, function (err) {
        console.log(err)
      })
    },

    setCurrent (row) {
      this.$refs.singleTable.setCurrentRow(row)
    },
    handleCurrentChange (val) {
      if (!val) {
        return
      }
      this.currentRow = val
      this.currentRow.checked = true
      console.log(val)
      this.changeTaskState(val)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.el-input {
    width: 300px;
}
 .el-row {
    margin-bottom: 20px;
    left: 38%;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    border-radius: 4px;
    margin-left: 2px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>



